body {
  background-color: #021621;

}
.Titles{
  text-shadow: 3px 0px 7px #56DBC3;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #ffffff;

}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #ee3472;
  border-radius: 10%;

}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
